import React, { useState, useEffect } from "react";
import Maps from "screens/Maps";
import Menubar from "components/navs/Menubar";
import Fab from "@mui/material/Fab";
import VpnKeyRoundedIcon from "@mui/icons-material/VpnKeyRounded";
import FaceRoundedIcon from "@mui/icons-material/FaceRounded";
const MainScreen = () => {
  const [map, setMap] = useState(null);
  const [mode, setMode] = useState("user");
  useEffect(() => {
    const _mode = localStorage.getItem("mode");
    setMode(_mode);
  }, []);

  const toggleMode = (text) => {
    localStorage.setItem("mode", text);
    setMode(text);
  };

  return (
    <div className='App'>
      {map && <Menubar map={map} />}
      <div
        style={{
          position: "absolute",
          width: "auto",
          height: "auto",
          borderRadius: "40px",
          backgroundColor: "red",
          right: "0",
          bottom: "0",
          zIndex: "2005",
          display: "flex",
          flexDirection: "row",
          justifyContent: "center",
          alignItems: "center",
          marginBottom: "15px",
          marginRight: "15px",
          cursor: "pointer",
        }}>
        {mode === "user" ? (
          <Fab
            variant='extended'
            color='warning'
            onClick={() => toggleMode("admin")}>
            <FaceRoundedIcon sx={{ mr: 1 }} />
            user
          </Fab>
        ) : (
          <Fab
            variant='extended'
            color='primary'
            onClick={() => toggleMode("user")}>
            <VpnKeyRoundedIcon sx={{ mr: 1 }} />
            admin
          </Fab>
        )}
      </div>
      <Maps setMap={setMap} />
    </div>
  );
};

export default MainScreen;
