import React, { useState, useEffect } from "react";
import { FiHome, FiChevronLeft, FiCompass, FiSettings } from "react-icons/fi";
import Sidebar from "components/navs/react-leaflet-sidetabs/Sidebar";
import Tab from "components/navs/react-leaflet-sidetabs/Tab";
import Button from "@mui/material/Button";

const SidebarComponent = ({ map }) => {
  const [openTab, setOpenTab] = useState("home");

  const onClose = () => {
    setOpenTab(false);
  };

  const onOpen = (id) => {
    setOpenTab(id);
  };

  useEffect(() => {
    setOpenTab("home");
  }, [localStorage.getItem("mode")]);

  return (
    <section className='Sidebar'>
      <Sidebar
        map={map}
        position='left'
        collapsed={!openTab}
        selected={openTab}
        closeIcon={<FiChevronLeft />}
        onClose={onClose}
        onOpen={onOpen}
        panMapOnChange
        rehomeControls>
        <Tab id='home' header='Home' icon={<FiHome />} active>
          <h3>React Leaflet Project</h3>
          <h4 style={{ marginTop: "5px" }}>
            현 프로젝트는 <b>React-leaflet</b>이 지도 이외의 실제 이미지
            내에서도 적용할 수 있도록 테스트하는 샘플 페이지입니다.
          </h4>
          <h4 style={{ marginTop: "5px" }}>
            이미지는 가상의 도면으로, 사무실 내 자리 배치 시스템으로
            가정하였습니다.
          </h4>
          <h4 style={{ marginTop: "5px" }}>
            <b>LocalStorage</b>를 사용하여 데이터를 관리하며, 간단한{" "}
            <b>marker</b> 사용 및 테스트를 진행할 수 있습니다.
          </h4>
          <h4 style={{ marginTop: "5px" }}>
            또한 우측 하단 버튼을 통해 모드 변경이 가능합니다. <br></br>
            (유저 모드, 관리자 모드)
          </h4>

          <h3>유저 모드</h3>
          <h4 style={{ marginTop: "5px" }}>
            유저 모드에서는 자리 별 배치와 <b>marker</b> 클릭 이벤트를 통한 정보
            확인이 가능합니다.
          </h4>

          <h3>관리자 모드</h3>
          <h4 style={{ marginTop: "5px" }}>
            관리자 모드로 전환 시 좌측 메뉴바에 있는{" "}
            <FiSettings size={16} style={{ marginBottom: "-2px" }} /> 아이콘이
            활성화됩니다.
          </h4>
          <h4 style={{ marginTop: "5px" }}>
            관리자는 다음과 같은 기능을 수행할 수 있습니다.
          </h4>
          <h4 style={{ marginTop: "20px" }}>
            <b>1. 인원 추가</b>
          </h4>
          <h4 style={{ marginTop: "5px" }}>
            - 추가 버튼 클릭 후 도면 상 원하는 지점 선택
            <br></br>- 해당 지점에 마커 자동 생성 후 팝업 창 로드
            <br></br>- 팝업 창에 인원 정보 입력 후 저장
            <br></br>- 정보 미입력 시 마커만 등록
          </h4>
          <h4 style={{ marginTop: "10px" }}>
            <b>2. 수정하기</b>
          </h4>
          <h4 style={{ marginTop: "5px" }}>
            - 수정하기 버튼 클릭 시 수정 모드로 변경
            <br></br>- drag event로 마커 위치 변경 가능
            <br></br>- click event로 마커 수정 팝업 로드
            <br></br>- 정보 미입력 시 마커만 등록
          </h4>
          <h4 style={{ marginTop: "10px" }}>
            <b>3. 삭제하기</b>
          </h4>
          <h4 style={{ marginTop: "5px" }}>
            - 수정모드에서 마커 클릭 시 나타나는 팝업 창에서 삭제 가능
          </h4>
        </Tab>
        <Tab
          id='settings'
          header='Settings'
          icon={<FiSettings />}
          anchor='top'
          disabled={localStorage.getItem("mode") === "user" ? true : false}>
          <h3>인원 추가</h3>
          <h4 style={{ marginTop: "5px" }}>
            <b>인원 추가하기</b> 버튼을 클릭한 후, 원하는 지점을 클릭하세요.
          </h4>
          <h4>추가 정보를 입력하지 않으면 해당 위치에 마커만 생성됩니다.</h4>
          <Button
            style={{ width: "100%", height: "35px", marginTop: "10px" }}
            variant='outlined'>
            인원 추가하기
          </Button>
          <h3>수정하기</h3>
          <h4 style={{ marginTop: "5px" }}>
            수정하기 버튼을 클릭한 후, 원하는 마커를 드래그하여 위치를 변경할 수
            있습니다.
          </h4>
          <Button
            style={{ width: "100%", height: "35px", marginTop: "10px" }}
            variant='outlined'>
            수정하기
          </Button>
        </Tab>
      </Sidebar>
    </section>
  );
};

export default SidebarComponent;
